<template lang="html">
            <div class="group1">
                <p class="tit">
                  第一步：司机信息
                </p>
                <div>
                  <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm" :inline="true">
                    <el-form-item label="注册手机号" prop="tel">
                      <el-input v-model="ruleForm.tel"></el-input>
                    </el-form-item><br />
                    <el-form-item label="司机姓名" prop="name">
                      <el-input v-model="ruleForm.name"></el-input>
                    </el-form-item>
                    <el-form-item label="身份证号" prop="ID">
                      <el-input v-model="ruleForm.ID"></el-input>
                    </el-form-item>
                    <el-form-item label="车牌号" prop="carnum">
                       <el-select v-model="ruleForm.carnum" placeholder="请选择车牌号">
                         <el-option label="宝马" value="shanghai"></el-option>
                         <el-option label="劳斯莱斯" value="beijing"></el-option>
                       </el-select>
                     </el-form-item>
                    <el-form-item label="车型" prop="style">
                       <el-select v-model="ruleForm.style" placeholder="请选择车型">
                         <el-option label="宝马" value="shanghai"></el-option>
                         <el-option label="劳斯莱斯" value="beijing"></el-option>
                       </el-select>
                     </el-form-item>
                     <el-form-item label="车长" prop="length">
                        <el-select v-model="ruleForm.length" placeholder="请选择区间">
                          <el-option label="5-6米" value="shanghai"></el-option>
                          <el-option label="6-7米" value="beijing"></el-option>
                        </el-select>
                      </el-form-item>
                      <el-form-item label="车高" prop="height">
                         <el-select v-model="ruleForm.height" placeholder="请选择区间">
                           <el-option label="3-4米" value="shanghai"></el-option>
                           <el-option label="5-6米" value="beijing"></el-option>
                         </el-select>
                       </el-form-item>
                       <el-form-item label="车宽" prop="weight">
                          <el-select v-model="ruleForm.weight" placeholder="请选择区间">
                            <el-option label="2-3米" value="shanghai"></el-option>
                            <el-option label="3-4米" value="beijing"></el-option>
                          </el-select>
                        </el-form-item>
                        <el-form-item label="吨位" prop="ton">
                           <el-select v-model="ruleForm.ton" placeholder="请选择区间">
                             <el-option label="宝马" value="shanghai"></el-option>
                             <el-option label="劳斯莱斯" value="beijing"></el-option>
                           </el-select>
                         </el-form-item>
                         <el-form-item label="期望流向" prop="direction">
                            <el-select v-model="ruleForm.direction" placeholder="请选择">
                              <el-option label="上海" value="shanghai"></el-option>
                              <el-option label="北京" value="beijing"></el-option>
                            </el-select>
                          </el-form-item>
                  </el-form>
                </div>
            </div>
</template>
<script>
   export default {
     data() {
    return {
      ruleForm: {
        tel:'',
        name: '',
        ID:'',
        style:'',
        carnum:'',
        length:'',
        height:'',
        weight:'',
        ton:'',
        direction:''
      },
      rules: {
        tel: [
          { required: true, message: '请输入手机号', trigger: 'blur' },
          { min: 11, max: 11, message: '请输入正确的手机号', trigger: 'blur' }
        ],
        name: [
          { required: true, message: '请输入司机姓名', trigger: 'blur' },
          { type:'string', message: '长度在 3 到 5 个字符', trigger: 'blur' }
        ],
        ID: [
          { required: true, message: '请输入身份证号', trigger: 'blur' },
          { min: 18, max: 18, message: '长度为18位', trigger: 'blur' }
        ],
        carnum: [
          { required: true, message: '请选择车牌号', trigger: 'change' }
        ],
        style: [
          { required: true, message: '请选择车型', trigger: 'change' }
        ],
        length: [
          { required: true, message: '请选择', trigger: 'change' }
        ],
        height: [
          { required: true, message: '请选择', trigger: 'change' }
        ],
        weight: [
          { required: true, message: '请选择', trigger: 'change' }
        ],
        ton: [
          { required: true, message: '请选择', trigger: 'change' }
        ],
        direction: [
          { required: true, message: '请选择', trigger: 'change' }
        ],
      }
    };
  },
   }
</script>
<style lang="less">
  *{
    margin:0;
    padding:0;
  }
  .group1{

        border-bottom: 1px solid #E8E8F2;
        .tit{
          font-size: 22px;
          color: #2f55a6;
          font-weight: 900;
          margin: 32px 0;
        }
        .el-form-item{
           margin-bottom: 20px;
           .el-input{
             width:230px;
           }
        }
        .el-form--inline .el-form-item__label{
          font-weight: bold;
          color: #9DA2C8;
        }
      }
</style>



