<template lang="html">
            <div class="money-group1">
                <p class="money-tit">
                  第二步：收款信息
                </p>
                <div>
                  <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm" :inline="true">
                    <el-form-item label="收款人姓名" prop="name">
                      <el-input v-model="ruleForm.name"></el-input>
                    </el-form-item>
                    <el-form-item label="收款银行" prop="bank">
                       <el-select v-model="ruleForm.bank" placeholder="请选择银行">
                         <el-option label="中国银行" value="shanghai"></el-option>
                         <el-option label="平安银行" value="beijing"></el-option>
                       </el-select>
                     </el-form-item>
                     <el-form-item label="银行卡号" prop="banknum">
                       <el-input v-model="ruleForm.banknum"></el-input>
                     </el-form-item>
                    <el-form-item label="开户行" prop="style">
                       <el-select v-model="ruleForm.khbank" placeholder="请选择银行">
                         <el-option label="中国银行" value="shanghai"></el-option>
                         <el-option label="平安银行" value="beijing"></el-option>
                       </el-select>
                     </el-form-item>
                     <el-form-item label="联系电话" prop="tel">
                       <el-input v-model="ruleForm.tel"></el-input>
                     </el-form-item>
                     <el-form-item label="开户行省份" prop="sheng">
                        <el-select v-model="ruleForm.sheng" placeholder="请选择区间">
                          <el-option label="上海" value="shanghai"></el-option>
                          <el-option label="北京" value="beijing"></el-option>
                        </el-select>
                      </el-form-item>
                      <el-form-item label="开户行城市" prop="city">
                         <el-select v-model="ruleForm.city" placeholder="请选择区间">
                           <el-option label="3-4米" value="shanghai"></el-option>
                           <el-option label="5-6米" value="beijing"></el-option>
                         </el-select>
                       </el-form-item>
                  </el-form>
                </div>
            </div>
</template>
<script>
   export default {
     data() {
    return {
      ruleForm: {
        name: '',
        bank:'',
        banknum:'',
        khbank:'',
        tel:'',
        sheng:'',
        city:''
      },
      rules: {
        name: [
          { required: true, message: '请输入收款人姓名', trigger: 'blur' },
          { type:'string', message: '长度在 3 到 5 个字符', trigger: 'blur' }
        ],
        bank: [
          { required: true, message: '请选择收款银行', trigger: 'change' }
        ],
        banknum:[
          { required: true, message: '请输入银行卡号', trigger: 'blur' }
        ],
        khbank: [
          { required: true, message: '请选择你开户的银行', trigger: 'change' }

        ],
        tel: [
          { required: true, message: '请输入手机号', trigger: 'blur' },
          { min: 11, max: 11, message: '请输入正确的手机号', trigger: 'blur' }
        ],
        sheng: [
          { required: true, message: '请选择省份', trigger: 'change' }
        ],
        city: [
          { required: true, message: '请选择城市', trigger: 'change' }
        ],
      }
    };
  },
   }
</script>
<style lang="less">
  *{
    margin:0;
    padding:0;
  }
  .money-group1{
        border-bottom: 1px solid #E8E8F2;
        .money-tit{
          font-size: 22px;
          color: #2f55a6;
          font-weight: 900;
          margin: 32px 0;
        }
        .el-form-item{
           margin-bottom: 20px;
           .el-input{
             width:230px;
           }
        }
        .el-form--inline .el-form-item__label{
          font-weight: bold;
          color: #9DA2C8;
        }
      }
</style>



